import { profileAPI, followStatus } from '../api/api';

const SET_USER_PROFILE = 'catSocial/profile/SET_USER_PROFILE';
const SET_STATUS = 'catSocial/profile/SET_STATUS';
const SET_FOLLOW_STATUS = 'catSocial/profile/SET_FOLLOW_STATUS';


let initState = {profile: null, followStatus: null, status:"Please enter yuor status"}

const profileReducer = (state = initState, action) => {
    switch (action.type) {
        case SET_USER_PROFILE: {
            return { ...state, profile: action.profile}
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
            case SET_FOLLOW_STATUS: {
            return {
                ...state,
                followStatus: action.followStatus
            }
        }
            default:
            return state;
    }
};

export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile });
export const setStatus = (status) => ({ type: SET_STATUS, status });
export const setFollowStatus = (followStatus) => ({ type: SET_FOLLOW_STATUS, followStatus });


export const userProfile = (userId) => async (dispatch) => {
     const data = await profileAPI.getProfile(userId)
        dispatch (setUserProfile(data));};
    
export const getStatus = (userId) => async (dispatch) => {
        const response = await profileAPI.getStatus(userId)
    dispatch(setStatus(response.data))
};
export const getFollowStatus = (userId) => async (dispatch) => {
        const response = await followStatus.getFollowStatus(userId)
        dispatch (setFollowStatus(response.data))};

export const updateStatus = (status) => async (dispatch) => {
       const response = await profileAPI.updateStatus(status)
                if (response.data.resultCode === 0) {
                    dispatch(setStatus(status))
                };}



export default profileReducer;
   