
import React, {PureComponent} from "react";
import { connect } from "react-redux";
import {  getBestFriends } from '../../redux/friendsReducer';
import Friends from "./Friends";

class FriendsContainer extends PureComponent {
    
    componentDidMount() {    
        
        this.props.getBestFriends();
        
    }
    // componentDidUpdate(prevProps) {
    //     console.log(prevProps.bestFriends)
    //     console.log(this.props.bestFriends)
    //     // if (prevProps.bestFriends.length !== this.props.bestFriends.length) {
    //     //     console.log('update');
    //     //     this.props.getBestFriends();
    //     // }
    //             }
    render() {     
        return (<>
            <Friends bestFriends={this.props.bestFriends}/>
        </>)
        }  
}

let mapStateToProps = (state) => {
    return {
        bestFriends: state.friendsPage.bestFriends,
        isAuth: state.auth.isAuth,
    }
}

export default 
    connect(mapStateToProps, {
        getBestFriends})
    (FriendsContainer);