import React from 'react';
import s from './Header.module.css';
import { Link } from 'react-router-dom';
import logo from '../../assets/images/1.jpg';
import myLogo from '../../assets/images/logo.jpg';
import noLogo from '../../assets/images/nologo.jpg';


const Header = (props) => {

    return (
        <header className={s.header}>
        <div>
        <Link to="/"><img src={logo} className={s.logo} alt="Logo" />
        </Link></div>
            <div><h1>First CatSocial network</h1></div>
            <span className={s.loginBlock}>
                <span className={s.login}>
                    {props.isAuth
                        ? <div>{props.login} <button onClick={props.logout}>LogOut</button></div>
                        : <Link to={'/login'}>Login</Link>}</span>
                <span><Link to={`/profile/` + props.id} className={`${s.navLink} ${s.linkHover}`}>
                    <img src={props.isAuth ? myLogo : noLogo} className={s.mylogo} alt="User Logo" /></Link></span>
            </span>
    </header>)
};

export default Header