import React from 'react';
import Preloader from '../../common/preloader/preloader';
import s from './Profile.module.css';
import userPhoto from '../../../assets/images/1.jpg';
import ProfileStatus from './ProfileStatus'
import ProfileStatusHooks from './ProfileStatusHooks';

const Profile = (props) => {
    if (!props.profile) {
        return <Preloader />;
    }
    return (<div className={s.content}>
        <div><img src={props.profile.photos.large!=null ? props.profile.photos.large : userPhoto} alt="User Large" /></div>
        <div className={s.userName}>{props.profile.fullName}</div>
        <div className={s.userId}>{props.profile.userId}</div>
        <div className={s.about}>{props.profile.aboutMe}</div>
        <ProfileStatusHooks status={props.status} updateStatus={props.updateStatus} />
        <div>
         {props.followStatus  
                            ? <button disabled={props.followInProgres.some(id => id === props.profile.userId)}
                                onClick={() => {props.unfollow(props.profile.userId)}}>Unfollow</button>
                            : <button disabled={props.followInProgres.some(id => id === props.profile.userId)}
                                onClick={() => {props.follow(props.profile.userId)}}>Follow</button>}
          </div>
        <ul className={s.contacts}>
            <li><a href={`https://${props.profile.contacts.facebook}`}>Facebook</a></li>
            <li><a href={props.profile.contacts.website}>Web site</a></li>
            <li><a href={props.profile.contacts.twitter}>twitter</a></li>
            <li><a href={`https://${props.profile.contacts.instagram}`}>Instagram</a></li>
        </ul>
    </div>);
};

export default Profile;